body{
  background-color: purple;
}
.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: cursive;
}

h1{
  font-size: 50px;
  color: white;
}

form{
  width: 450px;
  display: flex;
  height: 30px;
  padding: 5px;
  align-items: center;
  background-color: yellow;
  border-radius: 50px;
}

.inputText{
  padding: 0 10px;
  width: 90%;
  height: 100%;
  border-radius: 50px;
  margin-right: 5px;
  border: none;
}
.inputText:focus{
  outline: none;
}

.submit{
  border: none;
  background-color: transparent;
  color: purple;
  font-weight: bold;
}
.submit:focus{
  outline: none;
}

@keyframes delete{
  from{
    opacity: 1;
    height: 100%;
  }
  to{
    opacity: 0;
    height: 0;
  }
}

.todo{
  width: 450px;
  display: flex;
  margin: 10px 0;
  background-color: cyan;
  border-radius: 10px;
  padding-left: 10px;
  align-items: center;
}

.text{
  width: 90%;
  word-wrap: break-word;  
}

.delete{
  height: 40px;
  width: 40px;
  background-color: red;
  border-radius: 7px;
  border: none;
  color: white;
  font-weight: bold;
  font-size: 20px;
}
.delete:focus{
  outline: none;
}

#noTodo{
  color: white;
}

@media(max-width: 475px){
  form{
    width: 95vw
  }
  .todo{
    width: 95vw;
  }
}